export const extractErrorMessage = (err) => {
    let message = "An error occurred"; // Default message

    if (err?.response?.data) {
        const responseData = err.response.data;

        // Check if there's a direct message in the response
        if (responseData.message) {
            message = responseData.message;
        } else if (responseData.detail) {
            message = responseData.detail;
        } 
        // Check if there's an error inside `data` (like "non_field_errors")
        else if (typeof responseData.data === "object" && Object.keys(responseData.data).length) {
            const errorValues = Object.values(responseData.data);
            if (Array.isArray(errorValues[0]) && errorValues[0].length) {
                message = errorValues[0][0]; // Extract the first error message
            } else {
                message = JSON.stringify(errorValues[0]); // Fallback to string format
            }
        }
    } else if (err?.message) {
        message = err.message; // Fallback to generic error message
    }

    return message;
};


export default class REQUEST {
    async get(
        route,
        body = {},
        headers = {},
        onSuccess = Function.prototype,
        onError = Function.prototype,
        noToast
    ) {
        try {
            const response = await axios.get(BASE_URL + route, {
                headers: { Accept: "*/*", ...headers },
                data: body,
            });
            onSuccess(response);
            return response;
        } catch (err) {
            if (err.response.status === 401) {
                console.warn("logotu",err)
                setTimeout(() => {
                localStorage.setItem("t_token", "");
                window.location.replace("/login");
                }, 3000);
               
            }
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError && onError(result);
            return result;
        }
    }

    async post(
        route,
        body,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            let formData = body;
            if (
                headers["Content-Type"] === CONTENT_TYPES.MULTIPART_FORM ||
                headers["Content-Type"] === CONTENT_TYPES.URL_ENCODED
            ) {
                formData = getFormData(body, headers["Content-Type"]);
            }

            const response = await axios.post(BASE_URL + route, formData, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }

    async put(
        route,
        body,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            let formData = body;
            if (headers["Content-Type"] === CONTENT_TYPES.MULTIPART_FORM) {
                formData = getFormData(body);
            }
            const response = await axios.put(BASE_URL + route, formData, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }
    async patch(
        route,
        body,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            let formData = body;
            if (headers["Content-Type"] === CONTENT_TYPES.MULTIPART_FORM) {
                formData = getFormData(body);
            }
            const response = await axios.patch(BASE_URL + route, formData, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }

    async delete(
        route,
        headers = {},
        onSuccess = () => {},
        onError = () => {},
        noToast
    ) {
        try {
            const response = await axios.delete(BASE_URL + route, {
                headers: { Accept: "*/*", ...headers },
            });
            onSuccess(response);
            return response;
        } catch (err) {
            let message = extractErrorMessage(err);
            !noToast && toast.error(message || "Something might went wrong!");
            const result = { error: true, message: message };
            onError(result);
            return result;
        }
    }
}
